	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"Releasing new blog","date":"2017-04-19T09:04:38.000Z","tags":["blogging","JAMStack"]},"timeToRead":1,"html":"<p>It's a fact - I moved my blog from <a href=\"https://medium.com/@kalin.chernev\">Medium</a>\nto <a href=\"https://kalinchernev.github.io/\">github.com</a>!</p>\n<p>As previously mentioned <a href=\"/holiday-digest-2016\">last year</a>\nand <a href=\"/march-digest-2017\">last month</a>, I've been considering moving away from\nMedium as the platform is continuously shifting focus to vendors and businesses.\nSo, here it is - yet another blog, this time containing most of the content\npreviously published, just to make sure it does not disappear during the\ntransformation of Medium.</p>\n<h3 id=\"technology-stack\"><a href=\"#technology-stack\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Technology stack</h3>\n<p>Here's a brief overview of what's behind this blog:</p>\n<ul>\n<li>HTML</li>\n<li>CSS</li>\n<li>JavaScript</li>\n</ul>\n<p>That's all, generated by <a href=\"http://gohugo.io/\">Hugo</a>.</p>\n<p>The layout and design are not too different than before, rather, there are\nsome tweaks made on the way:</p>\n<ul>\n<li>Print-friendly, make it easy to read when pages are exported as PDF.</li>\n<li>PWA - you can \"install\" the site as an app and read content offline</li>\n<li>Code is highlighted</li>\n</ul>\n<p>Also, comments are integrated with <a href=\"https://disqus.com/\">Disqus</a>. I think\nthis is a particularly good improvement which maintains the community-oriented\nrecommendation and interaction flows.</p>\n<h3 id=\"conclusions\"><a href=\"#conclusions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Conclusions</h3>\n<p>With this new place to write, I plan to continue sharing about various experiments\nand tools I'm working on in the spirit of <a href=\"https://medium.com/digital-beer/digital-beer-2a3fc5a12006\">Digital Beer</a>.</p>"}},"pathContext":{"slug":"releasing-new-blog"}}

